export default {
  data() {
    return {
      // 手机号码输入的内容
      telVal: "",
      // 验证码输入的内容
      codeVal: "",
      // 点击发送的内容
      sendVal: "点击发送",
      // 倒计时内容
      time: 60,
      // 控制剩余时间的显示与隐藏
      isTime: false,
      // 控制点击发送的显示与隐藏
      isStart: true
    };
  },
  methods: {
    // 点击提交按钮
    submitVal() {
      if (this.telVal !== "" && this.codeVal !== "") {
        return uni.navigateTo({
          url: "/pages/mineVerificationCodeSuccess/mineVerificationCodeSuccess"
        });
      }
      return uni.showToast({
        title: "请输入手机号码或验证码",
        duration: 2000,
        icon: "none"
      });
    },
    start() {
      this.isTime = !this.isTime;
      this.isStart = !this.isStart;
    }
  },
  computed: {
    isActive() {
      if (this.telVal === "" || this.codeVal === "") {
        return false;
      }
      return true;
    }
  }
};
